import { Component, OnInit } from "@angular/core";
import { DatePipe } from "@angular/common";
import { HttpErrorResponse } from "@angular/common/http";
import * as $ from "jquery";
import { CreateQuoteService } from 'src/app/core/services/create-quote-service';
import { CommonService } from 'src/app/core/services/common-service';
import { DiscussionForumService } from 'src/app/core/services/discussion-forum.service';
import { TopicPost } from 'src/app/Models/post.model';
@Component({
  // selector: 'app-create-quote',
  templateUrl: "./create-post.component.html",
  styleUrls: ["./create-post.component.css"],
})
export class CreatePostComponent implements OnInit {
  sucesserror: string="";
  objPost=new TopicPost();
  respone: any;
  constructor(
    public datepipe: DatePipe,
    private createQuoteService: CreateQuoteService, 
    private commonService: CommonService,
    private discussionfourmService: DiscussionForumService)
    {
    
    }
 
  ngOnInit() 
  {
   this.getTopicList();
  }

  onSectionChange(event)
  {
    this.objPost.id = event.target.value;
    console.log(this.objPost.id);
  }

  topicList: any;
  getTopicList() 
  {
    this.discussionfourmService.getAllTopics().subscribe((res) => {
      this.topicList = res;
      console.log(res);
    });
  }

  createPostDetails()
  {
    if(this.objPost.id == "" || this.objPost.id == null)
    {
      this.sucesserror="Please select Topic for creating Post";
      $("#AlertModal").show();
      return;
    }
    if(this.objPost.content == "" || this.objPost.content == null)
    {
      this.sucesserror="Please enter content";
      $("#AlertModal").show();
      return;
    }
    this.discussionfourmService.createPostDetails(this.objPost).subscribe((res) => {
    this.respone=res; 
    console.log(this.respone)
    this.objPost.content="";
    this.objPost.id="";
    this.sucesserror="Post Created Successfully";
    $("#AlertModal").show();
    });
  }
}
